import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IProduct, IProductReq} from "../models/product.model";
import {InventoryEndpoints} from "../../inventory.endpoints";

@Injectable({
  providedIn: 'root'
})
export class ProductService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.PRODUCTS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<IProduct>>>(url)
      .subscribe((products: ResponseData<IPage<IProduct>>) => {
        this.eventBusService.publish(key, products.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, endpoint: string, id: number) {
    this.http.get<ResponseData<IProduct>>(InventoryEndpoints.PRODUCTS.GET(endpoint, id))
      .subscribe((product: ResponseData<IProduct>) => {
        this.eventBusService.publish(key, product.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, endpoint, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, endpoint: string, productReq: IProductReq, exId: number | null) {
    let url = exId == null ? InventoryEndpoints.PRODUCTS.CREATE(endpoint) : InventoryEndpoints.PRODUCTS.UPDATE(endpoint,exId)
    let observable = exId == null ? this.http.post<number>(url, productReq)
      : this.http.patch(url, productReq)
    console.log(productReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, endpoint, productReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

  approve(key: string, endpoint: string, id: number, approved: boolean) {
    let url = InventoryEndpoints.PRODUCTS.APPROVE(endpoint, id, approved)
    this.http.patch<ResponseData<Boolean>>(url, {}).subscribe(res => {
      this.eventBusService.publish(key, res.body)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.approve(key, endpoint, id, approved)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
