import {Component} from '@angular/core';
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {defaultVendorConfig, selfRoutes, VendorConfig} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {VendorUpsertComponent} from "../vendor-upsert/vendor-upsert.component";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {IVendor, IVendorBrief} from "../../models/vendor.model";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {VendorService} from "../../services/vendor.service";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-vendor-list',
  templateUrl: './vendor-list.component.html',
  styleUrls: ['./vendor-list.component.scss']
})
export class VendorListComponent extends BaseListComponentV2<IVendor> {

  config: VendorConfig = defaultVendorConfig
  filterData: BasicFilterData = defaultFilterData

  vendors: IPage<IVendorBrief> = defaultPage

  protected componentName(): string {
    return "VendorListComponent";
  }

  /*
    - Use endoints in AdminEndpoint class and delete it afterward
     ```
       static VENDORS = {
          VENDOR_ENDPOINT: environment.base_url + '/api/v1/vendors',
          VENDOR_ENDPOINT_ADMIN: environment.base_url + '/api/v1/admin/vendors',
          SEARCH(endpoint: string, params: IParams) {
            return endpoint.concat(BaseEndpoint.toParamString(params));
          },
          CREATE(): string {
            return this.VENDOR_ENDPOINT
          },
          GET(id: number): string {
            return this.VENDOR_ENDPOINT + '/' + id
          },
          UPDATE(id: number): string {
            return this.VENDOR_ENDPOINT + '/' + id
          },
        }
      ```
   */

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IVendor>,
              private activatedRoute: ActivatedRoute,
              private vendorService: VendorService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.showApprovalButton = true
      this.config.searchEndpoint = InventoryEndpoints.VENDORS.VENDOR_ENDPOINT_ADMIN_V2
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.fetchVendors({
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Vendors",
        link: null,
        active: true
      }
    ]
  }

  private fetchVendors(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.VENDORS.SEARCH(params))
    this.vendorService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.vendors = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Vendor is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IVendor>> | null {
    return VendorUpsertComponent
  }

  toggleApproval(id: number, approved: boolean) {
    this.loading = true
    const key = this.componentKey(EventKey.VENDORS.TOGGLE_APPROVAL(id))
    this.vendorService.toggleApproval(key, id, approved)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false
      this.notifier.success("Success", "Vendor is " + (approved ? "approved" : "disapproved"))
      this.ngOnInit()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error("Error", meta.data.error.message)
    })
  }
}
