import {Component, Inject} from '@angular/core';
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {AttributeConfig, defaultAttributeConfig, selfRoutes} from "../../models/config.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AttributeService} from "../../services/attribute.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IAttribute} from "../../models/attribute.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-attribute-upsert',
  templateUrl: './attribute-upsert.component.html',
  styleUrls: ['./attribute-upsert.component.scss']
})
export class AttributeUpsertComponent extends BaseUpsertComponentV2<IAttribute> {

  config: AttributeConfig = defaultAttributeConfig

  attributeForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    description: new FormControl('', [Validators.required, Validators.minLength(20)]),
    priority_level: new FormControl<number | null>(1,[Validators.min(0), Validators.max(10)]),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private attributeService: AttributeService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
  }

  protected override componentName(): string {
    return "AttributeUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Attributes",
        link: "/admin/attributes",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.ATTRIBUTES.GET(exId))
    this.attributeService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.attributeForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.ATTRIBUTES.UPSERT(exId))
    // @ts-ignore
    this.attributeService.upsert(key, this.attributeForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Attribute has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.attributeForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IAttribute) {
    this.attributeForm.controls.name.setValue(n.name)
    this.attributeForm.controls.description.setValue(n.description)
    this.attributeForm.controls.priority_level.setValue(n.priority_level)
  }

}
