import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfigV2} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";


export interface CategoryConfig extends BaseConfigV2 {
  showUpdateButton: boolean
  endpointV2: string
}

export let defaultCategoryConfig: CategoryConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  showUpdateButton: false,
  endpoint: InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT,
  endpointV2: InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT_V2,
  delEndpoint: InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/inventory/categories",
  getChildren(id: number) {
    return "/admin/inventory/categories?parent_id=" + id
  },
  create: "/admin/inventory/categories/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/inventory/categories/" + id + '/update'
  }
}
