import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultVariationConfig, selfRoutes, VariationConfig} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {VariationService} from "../../services/variation.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {IVariation} from "../../models/variation.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-variation-upsert',
  templateUrl: './variation-upsert.component.html',
  styleUrls: ['./variation-upsert.component.scss']
})
export class VariationUpsertComponent extends BaseUpsertComponentV2<IVariation> {

  config: VariationConfig = defaultVariationConfig

  variationForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    description: new FormControl('', [Validators.required, Validators.minLength(10)]),
    image: new FormControl<string | null>(null)
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private variationService: VariationService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.name!=null){
      this.variationForm.controls.name.setValue(dialogData.name)
    }
  }

  protected override componentName(): string {
    return "VariationUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Variations",
        link: "/admin/variations",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.VARIATIONS.GET(exId))
    this.variationService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.variationForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.variationForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.variationForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  /*
  - Add this to EventKey class and delete it afterward
     static VARIATIONS = {
      UPSERT(exId: number | null): string {
        return 'variations:upsert:' + exId
      },
      GET(exId: number): string {
        return 'variations:get-single:' + exId
      }
    }
*/
  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.VARIATIONS.UPSERT(exId))
    // @ts-ignore
    this.variationService.upsert(key, this.variationForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Variation has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.variationForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IVariation) {
    this.variationForm.controls.name.setValue(n.name)
    this.variationForm.controls.description.setValue(n.description)
    this.variationForm.controls.image.setValue(n.image == null ? null : n.image)
  }

}
