import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultVendorConfig, selfRoutes, VendorConfig} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {IVendor} from "../../models/vendor.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {VendorService} from "../../services/vendor.service";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {LocationTypeService} from "../../../../geo/locationtypes/services/locationtype.service";
import {ILocationType} from "../../../../geo/locationtypes/models/locationtype.model";
import {ILocation} from "../../../../geo/locations/models/location.model";
import {LocationService} from "../../../../geo/locations/services/location.service";
import {IUserBrief} from "../../../../users/users/models/user.model";

@Component({
  selector: 'app-vendor-upsert',
  templateUrl: './vendor-upsert.component.html',
  styleUrls: ['./vendor-upsert.component.scss']
})
export class VendorUpsertComponent extends BaseUpsertComponentV2<IVendor> {

  config: VendorConfig = defaultVendorConfig

  vendorForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    type: new FormControl('GENERAL', [Validators.required]),
    commission_percentage: new FormControl<number>(0, [Validators.required, Validators.min(0), Validators.max(100)]),
    description: new FormControl('', [Validators.required, Validators.minLength(10)]),
    logo: new FormControl<string | null>(null, [Validators.required]),
    banner: new FormControl<string | null>(null),

    owner_id: new FormControl<number | null>(null, []),

    supplier: new FormControl<boolean>(false, []),
    flagship: new FormControl<boolean>(false, []),
    authorized_dealer: new FormControl<boolean>(false, []),
    ecommerce_activated: new FormControl<boolean>(false, []),

    phone: new FormControl<string | null>(null, []),

    account_name: new FormControl<string | null>(null, []),
    account_number: new FormControl<string | null>(null, []),
    bank_name: new FormControl<string | null>(null, []),
    branch_name: new FormControl<string | null>(null, []),
    routing: new FormControl<string | null>(null, []),

    // open_schedules: new FormControl<IOpenSchedule[]>([],[]),

    address: new FormGroup({
      address_line_one: new FormControl<string | null>(null, [Validators.required]),
      address_line_two: new FormControl<string | null>(null, []),
      altitude: new FormControl<number>(0, [Validators.required]),
      latitude: new FormControl<number>(0, [Validators.required]),
      longitude: new FormControl<number>(0, [Validators.required]),
      title: new FormControl<string | null>(null, []),
      zip_code: new FormControl<string | null>(null, []),
      location_id: new FormControl<number | null>(null, []),
    }),

  })

  selectedOwner: IUserBrief | null = null

  locations: {
    type: ILocationType,
    locations: ILocation[],
    selectedLocation: ILocation | null
  }[] = []

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private vendorService: VendorService,
              private notifier: NotifierService,
              private locationTypeService: LocationTypeService,
              private locationService: LocationService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.title != null) {
      this.vendorForm.controls.name.setValue(dialogData.name)
    }

  }

  protected override componentName(): string {
    return "VendorUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Vendors",
        link: "/admin/vendors",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();

    this.fetchLocationTypes({page: 0, size: 100, q: null})
  }

  override initialize(exId: number): void {
    this.loading = true
    this.fetchEx(exId)
  }

  onSubmit() {
    if (!this.vendorForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onLogoPicked(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.vendorForm.controls.logo.setValue(images[0].image_url)
    }
  }

  onBannerPicked(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.vendorForm.controls.banner.setValue(images[0].image_url)
    }
  }

  getExLogo(): IImageSlice[] {
    let image = this.vendorForm.controls.logo.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getExBanner(): IImageSlice[] {
    let image = this.vendorForm.controls.banner.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }


  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.VENDORS.UPSERT(exId))
    // @ts-ignore
    this.vendorService.upsert(key, this.vendorForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Vendor has been " + (exId ? "updated" : "created"))
      this.vendorForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IVendor) {
    this.vendorForm.controls.name.setValue(n.name)
    this.vendorForm.controls.type.setValue(n.type)
    this.vendorForm.controls.commission_percentage.setValue(n.commission_percentage)
    this.vendorForm.controls.description.setValue(n.description)
    this.vendorForm.controls.logo.setValue(n.logo == null ? null : n.logo)
    this.vendorForm.controls.banner.setValue(n.banner == null ? null : n.banner)
    this.vendorForm.controls.owner_id.setValue(n.owner == null ? null : n.owner.id)
    this.vendorForm.controls.supplier.setValue(n.supplier)
    this.vendorForm.controls.flagship.setValue(n.flagship)
    this.vendorForm.controls.authorized_dealer.setValue(n.authorized_dealer)
    this.vendorForm.controls.ecommerce_activated.setValue(n.ecommerce_activated)
    this.vendorForm.controls.phone.setValue(n.phone)
    this.vendorForm.controls.account_name.setValue(n.account_name)
    this.vendorForm.controls.account_number.setValue(n.account_number)
    this.vendorForm.controls.bank_name.setValue(n.bank_name)
    this.vendorForm.controls.branch_name.setValue(n.branch_name)
    this.vendorForm.controls.routing.setValue(n.routing)

    this.vendorForm.controls.address.controls.address_line_one.setValue(n.address.address_line_one)
    this.vendorForm.controls.address.controls.address_line_two.setValue(n.address.address_line_two)
    this.vendorForm.controls.address.controls.altitude.setValue(n.address.altitude)
    this.vendorForm.controls.address.controls.latitude.setValue(n.address.latitude)
    this.vendorForm.controls.address.controls.longitude.setValue(n.address.longitude)
    this.vendorForm.controls.address.controls.title.setValue(n.address.title)
    this.vendorForm.controls.address.controls.zip_code.setValue(n.address.zip_code)
    this.vendorForm.controls.address.controls.location_id.setValue(n.address.location_id)

    this.selectedOwner = n.owner

    /*
    Set the selected locations to the dynamic location form fields
     */
    new Map<string, ILocation>(Object.entries(n.address.locations_tree))
      .forEach((location: ILocation, typeCode: string) => {
        let type = this.locations.find(l => l.type.code == typeCode)?.type
        if (type != null) {
          this.onLocationSelected(location, type)
        }
      })
  }

  private fetchEx(exId: number) {
    let key = this.componentKey(EventKey.VENDORS.GET(exId))
    this.vendorService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  private fetchLocationTypes(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONTYPES.SEARCH(params))
    this.locationTypeService.search(key, this.config.locationTypeEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let locationTypes = meta.data.content as ILocationType[]
        locationTypes.sort((a, b) => {
          return a.level - b.level
        })
        this.locations = locationTypes.map(type => {
          return {
            type: type,
            locations: [],
            selectedLocation: null
          }
        });
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  private fetchChildLocations(params: IParams, onLoaded: (locations: ILocation[]) => void) {
    this.loading = true
    const key = this.componentKey(EventKey.LOCATIONS.SEARCH(params))
    this.locationService.searchChildren(key, this.config.locationEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let locations = meta.data.content as ILocation[]
        onLoaded(locations)
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onLocationSelected(location: ILocation | null, type: ILocationType) {

    /*
     *  Set the selected location
     *  Unset all selected locations for other types where type.level > selectedType.level
     */
    this.locations = this.locations.map(l => {
      if (l.type.id == type.id) {
        return {
          ...l,
          selectedLocation: location
        }
      } else {
        if (l.type.level > type.level) {
          return {
            ...l,
            selectedLocation: null
          }
        }
        return l
      }
    })

    console.log('selected location', this.locations)

    /*
    Set the location id in the form where level is the highest
     */
    let sortedAndFiltered = this.locations.filter(l => l.selectedLocation != null)
      .sort((a, b) => {
        return b.type.level - a.type.level
      })

    console.log('sorted and filtered', sortedAndFiltered)

    if (sortedAndFiltered.length > 0) {
      let selectedLocation = sortedAndFiltered[0].selectedLocation
      this.vendorForm.controls.address.controls.location_id.setValue(
        selectedLocation == null ? null : selectedLocation.id
      )
    }

    console.log('selected location in control', this.vendorForm.controls.address.controls.location_id.value)

    if (location != null) {
      this.fetchChildLocations({page: 0, size: 100, q: null, parent_id: location.id}, locations => {
        if (locations.length == 0) {
          return
        }
        let childLocationTypeId = locations[0].type_id
        this.locations = this.locations.map(l => {
          if (l.type.id == childLocationTypeId) {
            return {
              ...l,
              locations: locations
            }
          } else {
            return l
          }
        })
      })
    }
  }

  onOwnerSelected(user: IUserBrief | null) {
    this.selectedOwner = user
    this.vendorForm.controls.owner_id.setValue(
      user == null ? null : user.id
    )
  }
}
