import {Component, EventEmitter, Input, Output} from "@angular/core";
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {IFilterableAttribute} from "../../models/filterableattribute.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {FilterableAttributeService} from "../../services/filterableattribute.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {FilterableAttributeUpsertComponent} from "../filterableattribute-upsert/filterableattribute-upsert.component";


@Component({
  selector: 'app-fa-select',
  templateUrl: './f-attribute-select.component.html',
  styleUrls: ['f-attribute-select.component.scss']
})
export class FAttributeSelectComponent extends BaseComponent {

  @Input() searchEndpoint: string = InventoryEndpoints.FILTERABLEATTRIBUTES.FILTERABLEATTRIBUTE_ENDPOINT

  items: IFilterableAttribute[] = []
  @Input() selectedItem: IFilterableAttribute | null = null
  @Output() selected: EventEmitter<IFilterableAttribute | null> = new EventEmitter<IFilterableAttribute | null>()

  protected override componentName(): string {
    return "FAttributeSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private filterableAttributeService: FilterableAttributeService,
              private popupService: DialogService<IFilterableAttribute>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = InventoryEndpoints.FILTERABLEATTRIBUTES.FILTERABLEATTRIBUTE_ENDPOINT_ADMIN
    }
  }

  override ngOnInit(): void {
    this.loading = false

    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.items.find(item => item.name == event.name)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        FilterableAttributeUpsertComponent, {id: null, name: event.name, dialogUpsert: true}
      )
      popup.componentInstance.onCompleted.subscribe((fa: IFilterableAttribute | null) => {
          this.selected.emit(fa)
        }
      )
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.FILTERABLEATTRIBUTES.SEARCH(params))
    this.filterableAttributeService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
