import {Component, Inject} from "@angular/core";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultProductConfig, ProductConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {ProductService} from "../../services/product.service";
import {IProduct, IProductReq, IProductSingle} from "../../models/product.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {ICategoryBrief} from "../../../categories/models/category.model";
import {IBrand} from "../../../brands/models/brand.model";
import {IFilterableAttribute} from "../../../filterableattributes/models/filterableattribute.model";
import {IVariation} from "../../../variations/models/variation.model";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {mapToParams} from "../../../../../../../base/endpoints/iparams";


@Component({
  selector: 'app-product-upsert',
  templateUrl: './product-upsert.component.html',
  styleUrls: ['./product-upsert.component.scss']
})
export class ProductUpsertComponent extends BaseUpsertComponentV2<IProduct> {

  config: ProductConfig = defaultProductConfig

  selectedCategories: ICategoryBrief[] = []
  selectedBrand: IBrand | null = null
  specifications: { attr: IFilterableAttribute, value: string | null }[] = []
  selectedVariations: IVariation[] = []

  productForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    slug: new FormControl('', [Validators.required, Validators.minLength(3)]),
    brand_id: new FormControl<number | null>(null, [Validators.required]),
    description: new FormControl('', []),
    category_ids: new FormControl<number[]>([], [Validators.required]),
    variant_ids: new FormControl<number[]>([], []),
    specifications: new FormControl<Map<string, string>>(new Map<string, string>(), []),
    images: new FormControl<string[]>([]),
    weight: new FormControl<number | null>(null),
    approved: new FormControl<boolean>(false),
    frozen_item: new FormControl<boolean>(false),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              authService: AuthService,
              private productService: ProductService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
    if (authService.isAdmin()) {
      this.config.getEndpoint = InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_ADMIN_V2
    }

    if (dialogData.name != null) {
      this.productForm.controls.name.setValue(dialogData.name)
    }
  }

  protected override componentName(): string {
    return "ProductUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Products",
        link: "/admin/products",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();

    this.productForm.controls.name.valueChanges.subscribe(value => {
      this.productForm.controls.slug.setValue(
        TextUtility.slugify(value) + "_" + TextUtility.randomString(5)
      )
    })
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTS_V2.GET(exId))
    this.productService.fetchSingle(key, this.config.getEndpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.productForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */

      this.productForm.controls.images.setValue(images.map(i => i.image_url))
    }
  }

  getExImages(): IImageSlice[] {
    let images: string[] = this.productForm.controls.images.value!!
    return images?.map((img: string) => {
      return {
        image_url: img,
        thumb_url: img,
      }
    })
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTS_V2.UPSERT(exId))

    let req: IProductReq = {
      name: this.productForm.controls.name.value!!,
      slug: this.productForm.controls.slug.value!!,
      brand_id: this.productForm.controls.brand_id.value!!,
      description: this.productForm.controls.description.value!!,
      category_ids: this.productForm.controls.category_ids.value!!,
      variant_ids: this.productForm.controls.variant_ids.value!!,
      images: this.productForm.controls.images.value!!,
      weight: this.productForm.controls.weight.value!!,
      approved: this.productForm.controls.approved.value!!,
      frozen_item: this.productForm.controls.frozen_item.value!!,
      action: null,
      specifications: mapToParams(this.productForm.controls.specifications.value!!)
    }

    this.productService.upsert(key, InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT, req, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "Product has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.productForm.reset();

      this.onSuccess(meta.data)

    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IProductSingle) {
    this.selectedBrand = n.brand
    this.selectedCategories = n.categories
    this.selectedVariations = n.variations
    this.specifications = n.specifications.map(s => {
      return {
        attr: s.filterable_attribute,
        value: s.value
      }
    })
    this.productForm.controls.name.setValue(n.name)
    this.productForm.controls.slug.setValue(n.slug)
    this.productForm.controls.brand_id.setValue(n.brand.id)
    this.productForm.controls.description.setValue(n.description)
    this.productForm.controls.category_ids.setValue(n.categories.map(c => c.id))
    this.productForm.controls.variant_ids.setValue(n.variations.map(v => v.id))
    this.productForm.controls.specifications.setValue(
      this.specificationsToMap(this.specifications)
    )
    this.productForm.controls.images.setValue(n.images)
    this.productForm.controls.weight.setValue(n.weight)
    this.productForm.controls.approved.setValue(n.approved)
    this.productForm.controls.frozen_item.setValue(n.frozen_item)
  }

  onCategorySelected(category: ICategoryBrief | null) {
    // if category is null or already selected, return
    if (category == null || this.selectedCategories.find(c => c.id == category.id)) {
      return
    }
    this.selectedCategories.push(category)
    this.productForm.controls.category_ids.setValue(
      this.selectedCategories.map(c => c.id)
    )
  }

  onBrandSelected(brand: IBrand | null) {
    this.selectedBrand = brand
    this.productForm.controls.brand_id.setValue(
      this.selectedBrand == null ? null : this.selectedBrand.id
    )
  }

  onFaSelected(attr: IFilterableAttribute | null) {
    if (attr == null || this.specifications.find(s => s.attr.id == attr.id)) {
      return
    }
    this.specifications.push({
      attr: attr,
      value: null
    })
  }

  onVariationSelected(variation: IVariation | null) {
    if (variation == null || variation.id == null || this.selectedVariations.find(v => v.id == variation.id)) {
      return
    }
    this.selectedVariations.push(variation)
    this.productForm.controls.variant_ids.setValue(
      this.selectedVariations.map(v => v.id)
    )
  }

  removeCategory(item: ICategoryBrief) {
    this.selectedCategories = this.selectedCategories.filter(c => c.id != item.id)
    this.productForm.controls.category_ids.setValue(
      this.selectedCategories.map(c => c.id)
    )
  }

  removeVariation(item: IVariation) {
    this.selectedVariations = this.selectedVariations.filter(v => v.id != item.id)
    this.productForm.controls.variant_ids.setValue(
      this.selectedVariations.map(v => v.id)
    )
  }

  onValueSelected(fa: IFilterableAttribute, event: Event) {
    this.specifications = this.specifications.map(s => {
      if (s.attr.id == fa.id) {
        s.value = (event.target as HTMLInputElement).value
      }
      return s
    })
    this.updateControlValue()
  }

  removeSpecification(item: IFilterableAttribute) {
    this.specifications = this.specifications.filter(s => s.attr.id != item.id)
    this.updateControlValue()
  }

  private updateControlValue() {
    this.productForm.controls.specifications.setValue(
      this.specificationsToMap(this.specifications)
    )
  }

  specificationsToMap(specs: { attr: IFilterableAttribute, value: string | null }[]
  ): Map<string, string> {
    return specs.reduce((acc, obj) => {
      if (obj.value !== null) {
        acc.set(obj.attr.code, obj.value);
      }
      return acc;
    }, new Map<string, string>())
  }


}
