import {Component, Inject} from '@angular/core';
import {CategoryConfig, defaultCategoryConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IImageSlice} from "../../../../../../common/modules/cms/files/models/file.model";
import {ICategoryBrief, ICategoryDetail} from "../../models/category.model";
import {CategoryService} from "../../services/category.service";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";

@Component({
  selector: 'app-category-upsert',
  templateUrl: './category-upsert.component.html',
  styleUrls: ['./category-upsert.component.scss']
})
export class CategoryUpsertComponent extends BaseUpsertComponentV3<ICategoryDetail> {

  config: CategoryConfig = defaultCategoryConfig

  selectedParent: ICategoryBrief | null = null

  categoryForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    description: new FormControl('', []),
    parent_id: new FormControl<number | null>(null),
    image: new FormControl<string | null>(null),
    update_attributes_for_children: new FormControl<boolean>(false),
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private categoryService: CategoryService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.name!=null){
      this.categoryForm.controls.name.setValue(dialogData.name)
    }
  }

  protected override componentName(): string {
    return "CategoryUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Categorys",
        link: "/admin/categorys",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCT_CATEGORIES.GET(exId))
    this.categoryService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.categoryForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  onImgUploaded(images: IImageSlice[]) {
    if (images.length > 0) {
      /*
      Think about the thumbnail, this will only upload the image, not thumbnail
       */
      this.categoryForm.controls.image.setValue(images[0].image_url)
    }
  }

  getExImages(): IImageSlice[] {
    let image = this.categoryForm.controls.image.value
    if (image == null) {
      return []
    }
    return [{image_url: image, thumb_url: image}]
  }

  getConfig(): BaseConfigV2 {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCT_CATEGORIES.UPSERT(exId))
    // @ts-ignore
    this.categoryService.upsert(key, this.categoryForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.categoryForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: ICategoryDetail) {
    this.selectedParent = n.parent
    this.categoryForm.controls.name.setValue(n.name)
    this.categoryForm.controls.description.setValue(n.description)
    this.categoryForm.controls.parent_id.setValue(
      n.parent == null ? null : n.parent.id
    )
    this.categoryForm.controls.image.setValue(n.image == null ? null : n.image)
  }

  onParentSelected(parent: ICategoryBrief | null) {
    this.selectedParent = parent
    this.categoryForm.controls.parent_id.setValue(
      parent == null ? null : parent.id
    )
  }
}
