import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface StockConfig extends BaseConfig {
  vendorEndpoint: string
}

export let defaultStockConfig: StockConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  searchEndpoint: InventoryEndpoints.STOCKS.STOCK_ENDPOINT_V2,
  delEndpoint: InventoryEndpoints.STOCKS.STOCK_ENDPOINT,
  vendorEndpoint: InventoryEndpoints.VENDORS.VENDOR_ENDPOINT_V2
}


export let selfRoutes = {
  list: "/admin/inventory/stocks",
  create: "/admin/inventory/stocks/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/inventory/stocks/" + id + '/update'
  }
}
