import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {ICategoryBrief, ICategoryDetail} from "../../models/category.model";
import {CategoryService} from "../../services/category.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {CategoryUpsertComponent} from "../category-upsert/category-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";

@Component({
  selector: 'app-category-select',
  templateUrl: './category-select.component.html',
  styleUrls: ['./category-select.component.scss']
})
export class CategorySelectComponent extends BaseComponent {
  @Input() searchEndpoint: string = InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT_V2

  categories: ICategoryBrief[] = []
  @Input() selectedItem: ICategoryBrief | null = null
  @Output() selected: EventEmitter<ICategoryBrief | null> = new EventEmitter<ICategoryBrief | null>()

  protected override componentName(): string {
    return "CategorySelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private categoryService: CategoryService,
              private popupService: DialogService<ICategoryDetail>) {
    super(eventBus, router, subContainer)
    this.searchEndpoint = InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT_ADMIN_V2
  }


  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0,
        size: 10,
        q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.categories.find(c => c.name == event.name)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(CategoryUpsertComponent, {id: null, name: event.name})
      popup.componentInstance.onCompleted.subscribe((category: ICategoryDetail | null) => {
          this.selected.emit(category as unknown as ICategoryBrief)
        }
      )
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.CATEGORIES.SEARCH_CATEGORIES_FOR_FILTER())
    this.categoryService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.categories = meta.data.content
      this.loading = false
    })
  }
}
