import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {AdminEndpoint} from "../../../../../admin.endpoint";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {VendorUpsertComponent} from "../vendor-upsert/vendor-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {IVendor, IVendorBrief} from "../../models/vendor.model";
import {VendorService} from "../../services/vendor.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";

@Component({
  selector: 'app-vendor-select',
  templateUrl: './vendor-select.component.html',
  styleUrls: ['./vendor-select.component.scss']
})
export class VendorSelectComponent extends BaseComponent {
  @Input() enableAdd: boolean = false

  @Input() searchEndpoint: string = InventoryEndpoints.VENDORS.VENDOR_ENDPOINT_V2

  items: IVendorBrief[] = []
  @Input() selectedItem: IVendorBrief | null = null
  @Output() selected: EventEmitter<IVendorBrief | null> = new EventEmitter<IVendorBrief | null>()

  protected override componentName(): string {
    return "VendorSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              private authService: AuthService,
              private vendorService: VendorService,
              private popupService: DialogService<IVendor>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.enableAdd = true
      this.searchEndpoint = InventoryEndpoints.VENDORS.VENDOR_ENDPOINT_ADMIN_V2
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.items.find(c => c.name == event.name)) {
      this.selected.emit(this.selectedItem)
    } else {
      let dialogRef = this.popupService.open(
        VendorUpsertComponent, {id: null, title: event.title, dialogUpsert: true}
      )
      let component = dialogRef.componentInstance
      component.onCompleted.subscribe((vendor: IVendor | null) => {
        this.selected.emit(vendor as unknown as IVendorBrief)
      })
      component.onCanceled.subscribe(() => {
        dialogRef.close()
      })
    }
  }

  search(params: IParams) {
    let admin = this.authService.isAdmin()
    this.loading = true
    let key = this.componentKey(EventKey.VENDORS.SEARCH(params) + TextUtility.randomString(5))
    if (admin) {
      this.vendorService.search(key, this.searchEndpoint, params)
    } else {
      this.vendorService.myVendors(key, this.searchEndpoint, params)
    }
    this.subscribe(key, null, (value, meta) => {
      if (admin) {
        this.items = meta.data.content
      } else {
        this.items = meta.data
      }
      this.loading = false
    })
  }
}
