import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface ProductSpecificationConfig extends BaseConfig {
}

export let defaultProductSpecificationConfig: ProductSpecificationConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: InventoryEndpoints.PRODUCTSPECIFICATIONS.PRODUCTSPECIFICATION_ENDPOINT,
  delEndpoint: InventoryEndpoints.PRODUCTSPECIFICATIONS.PRODUCTSPECIFICATION_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/product-specifications",
  create: "/admin/product-specifications/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/product-specifications/" + id + '/update'
  }
}
