import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {IVariation} from "../../models/variation.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {VariationService} from "../../services/variation.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {VariationUpsertComponent} from "../variation-upsert/variation-upsert.component";

@Component({
  selector: 'app-variation-select',
  templateUrl: './variation-select.component.html',
  styleUrls: ['./variation-select.component.scss']
})
export class VariationSelectComponent extends BaseComponent {

  @Input() searchEndpoint: string = InventoryEndpoints.VARIATIONS.VARIATION_ENDPOINT

  items: IVariation[] = []
  @Input() selectedItem: IVariation | null = null
  @Output() selected: EventEmitter<IVariation | null> = new EventEmitter<IVariation | null>()

  protected override componentName(): string {
    return "VariationSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private variationService: VariationService,
              private popupService: DialogService<IVariation>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = InventoryEndpoints.VARIATIONS.VARIATION_ENDPOINT_ADMIN
    }
  }

  override ngOnInit(): void {
    this.loading = false

    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.items.find(c => c.name == event.name)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(VariationUpsertComponent, {id: null, name: event.name})
      popup.componentInstance.onCompleted.subscribe((variation: IVariation | null) => {
          this.selected.emit(variation)
        }
      )
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.VARIATIONS.SEARCH(params))
    this.variationService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
