import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IVendor, IVendorBrief, IVendorReq} from "../models/vendor.model";
import {InventoryEndpoints} from "../../inventory.endpoints";
import {ICustomer} from "../../../sales/customers/models/customer.model";

@Injectable({
  providedIn: 'root'
})
export class VendorService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.VENDORS.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<IVendorBrief>>>(url)
      .subscribe((vendor: ResponseData<IPage<IVendorBrief>>) => {
        this.eventBusService.publish(key, vendor.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  myVendors(key: string, endpoint: string, params: IParams={}) {
    let url = InventoryEndpoints.VENDORS.MY_VENDORS(endpoint, params)
    this.http.get<IVendorBrief[]>(url)
      .subscribe((vendor: IVendorBrief[]) => {
        this.eventBusService.publish(key, vendor)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  searchCustomers(key: string, params: IParams) {
    let url = InventoryEndpoints.VENDORS.SEARCH_CUSTOMERS(params)
    this.http.get<IPage<ICustomer>>(url)
      .subscribe((customers: IPage<ICustomer>) => {
        this.eventBusService.publish(key, customers)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.searchCustomers(key, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }


  fetchSingle(key: string, id: number) {
    this.http.get<ResponseData<IVendor>>(InventoryEndpoints.VENDORS.GET(id))
      .subscribe((vendor: ResponseData<IVendor>) => {
        this.eventBusService.publish(key, vendor.body)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  toggleApproval(key: string, id: number, approved: boolean) {
    this.http.patch<IVendor>(InventoryEndpoints.VENDORS.TOGGLE_APPROVAL(id, approved), {})
      .subscribe((vendor: IVendor) => {
        this.eventBusService.publish(key, vendor)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, vendorReq: IVendorReq, exId: number | null) {
    let endpoint = exId == null ? InventoryEndpoints.VENDORS.CREATE() : InventoryEndpoints.VENDORS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, vendorReq)
      : this.http.patch(endpoint, vendorReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, vendorReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
