import {Component, Inject} from '@angular/core';
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultProductSpecificationConfig, ProductSpecificationConfig, selfRoutes} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {ProductSpecificationService} from "../../services/productspecification.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IProductSpecification} from "../../models/productspecification.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {IFilterableAttribute} from "../../../filterableattributes/models/filterableattribute.model";
import {IProduct, IProductBrief} from "../../../products/models/product.model";

@Component({
  selector: 'app-productspecification-upsert',
  templateUrl: './productspecification-upsert.component.html',
  styleUrls: ['./productspecification-upsert.component.scss']
})
export class ProductSpecificationUpsertComponent extends BaseUpsertComponentV2<IProductSpecification> {

  config: ProductSpecificationConfig = defaultProductSpecificationConfig

  selectedAttr: IFilterableAttribute | null = null
  selectedProduct: IProductBrief | null = null

  specificationForm = new FormGroup({
    product_id: new FormControl<number | null>(null, [Validators.required]),
    filterable_attribute_id: new FormControl<number | null>(null, [Validators.required]),
    value: new FormControl<string | null>(null, [Validators.required])
  })

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private productSpecificationService: ProductSpecificationService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
  }

  protected override componentName(): string {
    return "ProductSpecificationUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "ProductSpecifications",
        link: "/admin/productspecifications",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTSPECIFICATIONS.GET(exId))
    this.productSpecificationService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.specificationForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTSPECIFICATIONS.UPSERT(exId))
    // @ts-ignore
    this.productSpecificationService.upsert(key, this.specificationForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      let message = "ProductSpecification has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.specificationForm.reset();

      if (this.config.upsertOnDialog) {
        this.onCompleted.emit(meta.data)
      } else {
        this.navigate(selfRoutes.list, value => {
          this.notifier.success("Success!", message)
        })
      }
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IProductSpecification) {
    this.selectedAttr = n.filterable_attribute
    // TODO: get product info from server
    // this.selectedProduct = n.product_id

    this.specificationForm.controls.product_id.setValue(n.product_id)
    this.specificationForm.controls.filterable_attribute_id.setValue(n.filterable_attribute_id)
    this.specificationForm.controls.value.setValue(n.value)
  }

  onAttrSelected(attr: IFilterableAttribute | null) {
    this.selectedAttr = attr
    this.specificationForm.controls.filterable_attribute_id.setValue(
      attr?.id == null ? null : attr.id
    )
  }

  onProductSelected(product: IProductBrief | null) {
    this.selectedProduct = product
    this.specificationForm.controls.product_id.setValue(
      product?.id == null ? null : product.id
    )
  }

}
