import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";


export interface FilterableAttributeConfig extends BaseConfig {
}

export let defaultFilterableAttributeConfig: FilterableAttributeConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: InventoryEndpoints.FILTERABLEATTRIBUTES.FILTERABLEATTRIBUTE_ENDPOINT,
  delEndpoint: InventoryEndpoints.FILTERABLEATTRIBUTES.FILTERABLEATTRIBUTE_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/filterable-attributes",
  create: "/admin/filterable-attributes/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/filterable-attributes/" + id + '/update'
  }
}
