import {Injectable} from '@angular/core';
import {ICategoryBrief, ICategoryReq} from "../models/category.model";
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage, ResponseData} from "../../../../../../base/models/IPage";
import {InventoryEndpoints} from "../../inventory.endpoints";
import {ILabel} from "../../../labels/models/label.model";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {ICategory} from "../../../../../webapp/models/inventory/ICategory";

@Injectable({
  providedIn: 'root'
})
export class CategoryService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.CATEGORIES.SEARCH(endpoint, params)
    this.http.get<ResponseData<IPage<ICategoryBrief>>>(url)
      .subscribe((categories: ResponseData<IPage<ICategoryBrief>>) => {
        this.eventBusService.publish(key, categories.body)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<ICategory>(InventoryEndpoints.CATEGORIES.GET(id))
      .subscribe((category: ICategory) => {
        this.eventBusService.publish(key, category)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchMultiple(key: string, ids: number[]) {
    this.http.get<ILabel[]>(InventoryEndpoints.CATEGORIES.GET_MULTIPLE(ids))
      .subscribe((label: ILabel[]) => {
        this.eventBusService.publish(key, label)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchMultiple(key, ids)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }


  upsert(key: string, categoryReq: ICategoryReq, exId: number | null) {
    let endpoint = exId == null ? InventoryEndpoints.CATEGORIES.CREATE() : InventoryEndpoints.CATEGORIES.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, categoryReq)
      : this.http.patch(endpoint, categoryReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, categoryReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
