import {Component} from "@angular/core";
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {defaultProductConfig, ProductConfig, selfRoutes} from "../../models/config.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {
  BasicFilterData, defaultFilterData,
  parseFilterData, toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {ProductUpsertComponent} from "../product-upsert/product-upsert.component";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {ProductService} from "../../services/product.service";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {IProduct, IProductBrief} from "../../models/product.model";
import {ICategoryBrief} from "../../../categories/models/category.model";

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent extends BaseListComponentV2<IProduct> {

  config: ProductConfig = defaultProductConfig
  filterData: BasicFilterData = defaultFilterData

  products: IPage<IProductBrief> = defaultPage

  protected componentName(): string {
    return "ProductListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IProduct>,
              private activatedRoute: ActivatedRoute,
              private productService: ProductService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.showApproveButton = true
      this.config.searchEndpoint = InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_ADMIN_V2
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      this.fetchProducts({
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: this.translations["Products"],
        link: null,
        active: true
      }
    ]
  }

  getCategoryNames(categories: ICategoryBrief[]): string {
    return categories.map(c => c.name).join(", ")
  }

  private fetchProducts(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.PRODUCTS_V2.SEARCH(params))
    this.productService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.products = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Product is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }


  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IProduct>> | null {
    return ProductUpsertComponent
  }

  onApproveClick(id: number, approved: boolean) {
    let key = this.componentKey(EventKey.PRODUCTS_V2.APPROVE(id))
    this.productService.approve(key, this.config.searchEndpoint, id, approved)
    this.subscribe(key, null, (value, meta) => {
      this.notifier.success("Success", "Product is approved")
      this.ngOnInit()
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error("Error", meta.data.error.message)
    })
  }
}
