import {Component, Inject, LOCALE_ID} from '@angular/core';
import {defaultStockConfig, selfRoutes, StockConfig} from "../../models/config.model";
import {
  BasicFilterData,
  defaultFilterData, FilterField,
  parseFilterData,
  toParams
} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {StockService} from "../../services/stock.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {StockUpsertComponent} from "../stock-upsert/stock-upsert.component";
import {IStock, IStockDetail,} from "../../models/stock.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {BaseListComponentV3} from "../../../../../../../base/components/base-list-v3.component";
import {ICategoryBrief} from "../../../categories/models/category.model";
import {IVendor} from "../../../vendors/models/vendor.model";
import {VendorService} from "../../../vendors/services/vendor.service";

@Component({
  selector: 'app-stock-list',
  templateUrl: './stock-list.component.html',
  styleUrls: ['./stock-list.component.scss']
})
export class StockListComponent extends BaseListComponentV3<IStock, IStockDetail> {

  config: StockConfig = defaultStockConfig
  filterData: BasicFilterData = defaultFilterData

  stocks: IPage<IStock> = defaultPage
  // vendors: IVendor[] = []

  extraFields: FilterField[] = [
    {
      name: 'vendor_id',
      type: 'dropdown',
      placeholder: 'Select Store',
      label: 'Store',
      value: null,
      options: []
    }
  ]

  protected componentName(): string {
    return "StockListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IStockDetail>,
              private activatedRoute: ActivatedRoute,
              private stockService: StockService,
              private vendorService: VendorService,
              private notifier: NotifierService,
              @Inject(LOCALE_ID) private locale: string
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = InventoryEndpoints.STOCKS.STOCK_ENDPOINT_ADMIN_V2
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      this.filterData = parseFilterData(params)
      let vendorId = this.filterData.extra['vendor_id']
      this.fetchMyVendors({}, (vendorId: number) => {
      })
      this.fetchStocks({
        vendor_id: vendorId,
        q: this.filterData.query,
        username: this.filterData.username,
        from_date: this.filterData.fromDate,
        to_date: this.filterData.toDate,
        page: this.filterData.page,
        size: this.filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: this.translations["Stocks"],
        link: null,
        active: true
      }
    ]
  }

  private fetchStocks(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.STOCKS.SEARCH(params))
    this.stockService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.stocks = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Stock is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }

  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: toParams(this.filterData)
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IStockDetail>> | null {
    return StockUpsertComponent
  }

  onCopyClick(sku: string) {
    TextUtility.copyToClipboard(sku)
    this.notifier.info("Copied", "SKU has been copied to clipboard")
  }

  getDiscountedRetailPrice(item: IStock): number {
    return item.retail_price - this.getDiscount(item)
  }

  getDiscount(stock: IStock): number {
    if (
      stock.discount_valid_until == null ||
      new Date(stock.discount_valid_until).getTime() > new Date().getTime()
    ) {
      return stock.retail_price * (stock.discount_percentage / 100)
    } else {
      return 0
    }
  }

  categoriesToString(categories: ICategoryBrief[]): string {
    return categories.map(category => category.name)
      .join(', ')
  }

  private fetchMyVendors(params: IParams = {}, block: (vendorId: number) => void) {
    let key = this.componentKey(EventKey.VENDORS.MY_VENDORS(params))
    this.vendorService.myVendors(key, this.config.vendorEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      let vendors = meta.data as IVendor[]
      this.extraFields = [
        {
          name: 'vendor_id',
          type: 'dropdown',
          label: 'Vendor',
          placeholder: 'Select Store',
          value: null,
          options: vendors.map(v => {
            return {
              label: v.name,
              value: v.id
            }
          })
        }
      ]
    })
  }
}
