import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {AdminEndpoint} from "../../../../admin.endpoint";
import {IPage} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IProductSpecification, IProductSpecificationReq} from "../models/productspecification.model";
import {InventoryEndpoints} from "../../inventory.endpoints";
@Injectable({
  providedIn: 'root'
})
export class ProductSpecificationService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.PRODUCTSPECIFICATIONS.SEARCH(endpoint, params)
    this.http.get<IPage<IProductSpecification>>(url)
      .subscribe((productSpecification: IPage<IProductSpecification>) => {
        this.eventBusService.publish(key, productSpecification)
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<IProductSpecification>(InventoryEndpoints.PRODUCTSPECIFICATIONS.GET(id))
      .subscribe((productSpecification: IProductSpecification) => {
        this.eventBusService.publish(key, productSpecification)
      }, (error: HttpErrorResponse) => {
         this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, productSpecificationReq: IProductSpecificationReq, exId: number | null) {
    let endpoint = exId == null ? InventoryEndpoints.PRODUCTSPECIFICATIONS.CREATE() : InventoryEndpoints.PRODUCTSPECIFICATIONS.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, productSpecificationReq)
      : this.http.patch(endpoint, productSpecificationReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, productSpecificationReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
