import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface VariationConfig extends BaseConfig {
  showUpdateButton: boolean
}

export let defaultVariationConfig: VariationConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  showUpdateButton: false,
  searchEndpoint: InventoryEndpoints.VARIATIONS.VARIATION_ENDPOINT,
  delEndpoint: InventoryEndpoints.VARIATIONS.VARIATION_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/inventory/variations",
  create: "/admin/inventory/variations/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/inventory/variations/" + id + '/update'
  }
}
