import {Component, EventEmitter, Input, Output, ViewChild} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {IStock, IStockDetail} from "../../models/stock.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {StockService} from "../../services/stock.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {StockUpsertComponent} from "../stock-upsert/stock-upsert.component";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {NgSelectComponent} from "@ng-select/ng-select";

@Component({
  selector: 'app-stock-select',
  templateUrl: './stock-select.component.html',
  styleUrls: ['./stock-select.component.scss']
})
export class StockSelectComponent extends BaseComponent {
  @ViewChild("stockSelect") stockSelect: NgSelectComponent | null = null

  @Input() enableAdd: boolean = true

  @Input() searchEndpoint: string = InventoryEndpoints.STOCKS.STOCK_ENDPOINT_V2

  items: IStock[] = []
  @Input() selectedItem: IStock | null = null
  @Output() selected: EventEmitter<IStock | null> = new EventEmitter<IStock | null>()

  protected override componentName(): string {
    return "StockSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private stockService: StockService,
              private popupService: DialogService<IStockDetail>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = InventoryEndpoints.STOCKS.STOCK_ENDPOINT_ADMIN_V2
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  clear() {
    this.stockSelect?.clearModel()
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    if (this.items.find(c => c.label == event.label)) {
      this.selected.emit(this.selectedItem)
    } else {
      let popup = this.popupService.open(
        StockUpsertComponent, {id: null, title: event.title}
      )
      popup.componentInstance.onCompleted.subscribe((stock: IStock | null) => {
          this.selected.emit(stock)
        }
      )
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.STOCKS.SEARCH(params))
    this.stockService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
