import {Component} from '@angular/core';
import {CategoryConfig, defaultCategoryConfig, selfRoutes} from "../../models/config.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {parseFilterData} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {CategoryUpsertComponent} from "../category-upsert/category-upsert.component";
import {ICategoryBrief, ICategoryDetail} from "../../models/category.model";
import {CategoryService} from "../../services/category.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {BaseConfigV2} from "../../../../../../../base/models/baseConfig";
import {ICategory} from "../../../../../../webapp/models/inventory/ICategory";
import {BaseListComponentV4} from "../../../../../../../base/components/base-list-v4.component";
import {BaseUpsertComponentV3} from "../../../../../../../base/components/base-upsert-v3.component";

@Component({
  selector: 'app-category-list',
  templateUrl: './category-list.component.html',
  styleUrls: ['./category-list.component.scss']
})
export class CategoryListComponent extends BaseListComponentV4<ICategoryBrief, ICategoryDetail> {

  config: CategoryConfig = defaultCategoryConfig
  categories: IPage<ICategoryBrief> = defaultPage

  protected componentName(): string {
    return "CategoryListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<ICategoryDetail>,
              private activatedRoute: ActivatedRoute,
              private categoryService: CategoryService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.endpoint = InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT_ADMIN
      this.config.endpointV2 = InventoryEndpoints.CATEGORIES.CATEGORY_ENDPOINT_ADMIN_V2
      this.config.showDeleteButton = true
      this.config.showUpdateButton = true
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      let parentId: number | null = params['parent_id'] ? parseInt(params['parent_id']) : null
      this.fetchCategories({
        q: filterData.query,
        parent_id: parentId,
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        page: filterData.page,
        size: filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return this.breadcrumbItems
  }

  private fetchCategories(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.PRODUCT_CATEGORIES.SEARCH(params))
    this.categoryService.search(key, this.config.endpointV2, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.categories = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  private getMultipleByIds(ids: number[]) {
    this.loading = true
    const key = this.componentKey(EventKey.CATEGORIES.GET_MULTIPLE(ids))
    this.categoryService.fetchMultiple(key, ids)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        let categories = meta.data as ICategory[]
        let breadcrumbItems: BreadcrumbItem[] = [{
          text: "Categories",
          link: selfRoutes.list,
          active: false
        }]
        categories.map(category => {
          // map label to breadcrumb item
          breadcrumbItems.push({
            text: category.name,
            link: selfRoutes.getChildren(category.id),
            active: false
          })
        })
        this.breadcrumbItems = breadcrumbItems
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "Category is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfigV2 {
    return this.config
  }


  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: {}
    }
  }


  getUpsertComponent(): ComponentType<BaseUpsertComponentV3<ICategoryDetail>> | null {
    return CategoryUpsertComponent
  }

  onCategoryClick(item: ICategoryBrief) {
    this.navigateByUrl(selfRoutes.getChildren(item.id), then => {
      this.getMultipleByIds(item.absolute_path.split(':').map(id => parseInt(id)))
    })
  }

}
