import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../../base/services/base.service";
import {AuthService} from "../../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../../base/endpoints/iparams";
import {IPage} from "../../../../../../base/models/IPage";
import {IAuth} from "../../../../../common/modules/auth/models/auth.model";
import {IFilterableAttribute, IFilterableAttributeReq} from "../models/filterableattribute.model";
import {InventoryEndpoints} from "../../inventory.endpoints";

@Injectable({
  providedIn: 'root'
})
export class FilterableAttributeService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = InventoryEndpoints.FILTERABLEATTRIBUTES.SEARCH(endpoint, params)
    this.http.get<IPage<IFilterableAttribute>>(url)
      .subscribe((filterableAttribute: IPage<IFilterableAttribute>) => {
        this.eventBusService.publish(key, filterableAttribute)
      }, error => {
        this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<IFilterableAttribute>(InventoryEndpoints.FILTERABLEATTRIBUTES.GET(id))
      .subscribe((filterableAttribute: IFilterableAttribute) => {
        this.eventBusService.publish(key, filterableAttribute)
      }, (error: HttpErrorResponse) => {
        this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, filterableAttributeReq: IFilterableAttributeReq, exId: number | null) {
    let endpoint = exId == null ? InventoryEndpoints.FILTERABLEATTRIBUTES.CREATE() : InventoryEndpoints.FILTERABLEATTRIBUTES.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, filterableAttributeReq)
      : this.http.patch(endpoint, filterableAttributeReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, filterableAttributeReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
