import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CategoryUpsertComponent} from "./categories/components/category-upsert/category-upsert.component";
import {CategoryListComponent} from "./categories/components/category-list/category-list.component";
import {AuthGuardService} from "../../../common/modules/auth/services/auth-guard.service";
import {AttributeComponent} from "./attributes/attribute.component";
import {AttributeUpsertComponent} from "./attributes/components/attribute-upsert/attribute-upsert.component";
import {FilterableAttributeComponent} from "./filterableattributes/filterableattribute.component";
import {
  FilterableAttributeUpsertComponent
} from "./filterableattributes/components/filterableattribute-upsert/filterableattribute-upsert.component";
import {BrandComponent} from "./brands/brand.component";
import {BrandUpsertComponent} from "./brands/components/brand-upsert/brand-upsert.component";
import {VariationComponent} from "./variations/variation.component";
import {VariationUpsertComponent} from "./variations/components/variation-upsert/variation-upsert.component";
import {ProductComponent} from "./products/product.component";
import {ProductUpsertComponent} from "./products/components/product-upsert/product-upsert.component";
import {ProductSpecificationComponent} from "./productspecifications/productspecification.component";
import {
  ProductSpecificationListComponent
} from "./productspecifications/components/productspecification-list/productspecification-list.component";
import {
  ProductSpecificationUpsertComponent
} from "./productspecifications/components/productspecification-upsert/productspecification-upsert.component";
import {StockComponent} from "./stocks/stock.component";
import {StockUpsertComponent} from "./stocks/components/stock-upsert/stock-upsert.component";
import {VendorComponent} from "./vendors/vendor.component";
import {VendorUpsertComponent} from "./vendors/components/vendor-upsert/vendor-upsert.component";

const routes: Routes = [
  {path: "categories", component: CategoryListComponent},
  {path: "categories/:id", component: CategoryUpsertComponent},
  {path: "categories/create", component: CategoryUpsertComponent},
  {path: 'attributes', component: AttributeComponent, canActivate: [AuthGuardService]},
  {path: 'attributes/create', component: AttributeUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'attributes/:id/update', component: AttributeUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'filterable-attributes', component: FilterableAttributeComponent, canActivate: [AuthGuardService]},
  {
    path: 'filterable-attributes/create',
    component: FilterableAttributeUpsertComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'filterable-attributes/:id/update',
    component: FilterableAttributeUpsertComponent,
    canActivate: [AuthGuardService]
  },
  {path: 'brands', component: BrandComponent, canActivate: [AuthGuardService]},
  {path: 'brands/create', component: BrandUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'brands/:id/update', component: BrandUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'variations', component: VariationComponent, canActivate: [AuthGuardService]},
  {path: 'variations/create', component: VariationUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'variations/:id/update', component: VariationUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'products', component: ProductComponent, canActivate: [AuthGuardService]},
  {path: 'products/create', component: ProductUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'products/:id/update', component: ProductUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'product-specifications', component: ProductSpecificationComponent, canActivate: [AuthGuardService]},
  {
    path: 'product-specifications/create',
    component: ProductSpecificationListComponent,
    canActivate: [AuthGuardService]
  },
  {
    path: 'product-specifications/:id/update',
    component: ProductSpecificationUpsertComponent,
    canActivate: [AuthGuardService]
  },
  {path: 'stocks', component: StockComponent, canActivate: [AuthGuardService]},
  {path: 'stocks/create', component: StockUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'stocks/:id/update', component: StockUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'vendors', component: VendorComponent, canActivate: [AuthGuardService]},
  {path: 'vendors/create', component: VendorUpsertComponent, canActivate: [AuthGuardService]},
  {path: 'vendors/:id/update', component: VendorUpsertComponent, canActivate: [AuthGuardService]},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InventoryRoute {
}
