import {Component, Inject} from '@angular/core';
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultStockConfig, selfRoutes, StockConfig} from "../../models/config.model";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {StockService} from "../../services/stock.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IStockDetail, IStockReq} from "../../models/stock.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {IVendor, IVendorBrief} from "../../../vendors/models/vendor.model";
import {IProduct, IProductBrief, toProductBrief} from "../../../products/models/product.model";
import {IVariation} from "../../../variations/models/variation.model";


@Component({
  selector: 'app-stock-upsert',
  templateUrl: './stock-upsert.component.html',
  styleUrls: ['./stock-upsert.component.scss']
})
export class StockUpsertComponent extends BaseUpsertComponentV2<IStockDetail> {

  config: StockConfig = defaultStockConfig

  stockForm = new FormGroup({
    vendor_id: new FormControl<number | null>(null, [Validators.required]),
    product_id: new FormControl<number | null>(null, [Validators.required]),
    supplier_id: new FormControl<number | null>(null, []),
    variation_id: new FormControl<number | null>(null, []),
    quantity: new FormControl<number | null>(null, [Validators.required]),
    unit_price: new FormControl<number | null>(null, [Validators.required]),
    retail_price: new FormControl<number | null>(null, [Validators.required]),
    discount_percentage: new FormControl<number | null>(null, []),
    discount_valid_until: new FormControl<Date | null>(null, []),
    cashback_percentage: new FormControl<number | null>(null, []),
    cashback_valid_until: new FormControl<Date | null>(null, []),
  })

  selectedVendor: IVendorBrief | null = null
  selectedSupplier: IVendor | null = null
  selectedProduct: IProductBrief | null = null
  selectedVariation: IVariation | null = null

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private stockService: StockService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)
  }

  protected override componentName(): string {
    return "StockUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "Stocks",
        link: "/admin/stocks",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.STOCKS.GET(exId))
    this.stockService.fetchSingle(key, this.config.searchEndpoint, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  currentDate(): Date {
    return new Date()
  }

  onSubmit() {
    console.log(this.stockForm.value)
    if (!this.stockForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.STOCKS.UPSERT(exId))
    let req = this.stockForm.value as IStockReq
    this.stockService.upsert(key, req, exId)
    this.subscribe(key, null, (value, meta) => {
      let message = "Stock has been " + (exId ? "updated" : "created")
      this.notifier.success("Success", message)
      this.stockForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IStockDetail) {

    this.selectedVendor = n.vendor
    this.selectedProduct = n.product
    this.selectedVariation = n.variation

    this.stockForm.controls.vendor_id.setValue(n.vendor_id)
    this.stockForm.controls.product_id.setValue(n.product_id)
    this.stockForm.controls.variation_id.setValue(n.variation_id)

    // set prices by default
    this.stockForm.controls.unit_price.setValue(
      n.avg_unit_price == null ? 0 : Math.round(n.avg_unit_price)
    )
    this.stockForm.controls.retail_price.setValue(n.retail_price)
    this.stockForm.controls.discount_percentage.setValue(n.discount_percentage)
    this.stockForm.controls.discount_valid_until.setValue(
      n.discount_valid_until == null ? null : n.discount_valid_until
    )
    this.stockForm.controls.cashback_percentage.setValue(n.cashback_percentage)
    this.stockForm.controls.cashback_valid_until.setValue(
      n.cashback_valid_until == null ? null : n.cashback_valid_until
    )
  }

  onVendorSelected(vendor: IVendorBrief | null) {
    this.selectedVendor = vendor
    this.stockForm.controls.vendor_id.setValue(vendor == null ? null : vendor.id)
  }

  onSupplierSelected(vendor: IVendor | null) {
    this.selectedSupplier = vendor
    this.stockForm.controls.supplier_id.setValue(vendor == null ? null : vendor.id)
  }

  onProductSelected(product: IProductBrief | null) {
    console.log(product)
    this.selectedProduct = product
    this.stockForm.controls.product_id.setValue(product == null ? null : product.id)
  }

  onVariationSelected(variation: IVariation | null) {
    this.selectedVariation = variation
    this.stockForm.controls.variation_id.setValue(variation == null ? null : variation.id)
  }

}
