import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";

export interface ProductConfig extends BaseConfig {
  getEndpoint: string,
  showApproveButton: boolean
}

export let defaultProductConfig: ProductConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: false,
  showUserFilter: false,
  showDeleteButton: true,
  showApproveButton: false,
  searchEndpoint: InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_V2,
  delEndpoint: InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_V2,
  getEndpoint: InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_V2,
}


export let selfRoutes = {
  list: "/admin/inventory/products",
  create: "/admin/inventory/products/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/inventory/products/" + id + '/update'
  }
}
