import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {InventoryEndpoints} from "../../inventory.endpoints";
import {AdminEndpoint} from "../../../../admin.endpoint";

export interface VendorConfig extends BaseConfig {
  locationTypeEndpoint: string
  locationEndpoint: string
  showApprovalButton: boolean
}

export let defaultVendorConfig: VendorConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: false,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: InventoryEndpoints.VENDORS.VENDOR_ENDPOINT,
  delEndpoint: InventoryEndpoints.VENDORS.VENDOR_ENDPOINT,
  locationTypeEndpoint: AdminEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT,
  locationEndpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT,
  showApprovalButton: false
}


export let selfRoutes = {
  list: "/admin/inventory/vendors",
  create: "/admin/inventory/vendors/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/inventory/vendors/" + id + '/update'
  }
}
