import {Component} from '@angular/core';
import {BaseListComponentV2} from "../../../../../../../base/components/base-list-v2.component";
import {defaultProductSpecificationConfig, ProductSpecificationConfig, selfRoutes} from "../../models/config.model";
import {defaultPage, IPage} from "../../../../../../../base/models/IPage";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {ProductSpecificationService} from "../../services/productspecification.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {parseFilterData} from "../../../../../../common/modules/utility/components/basic-filter/basic-filter.model";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {MetaData} from "ng-event-bus/lib/meta-data";
import {ComponentType} from "@angular/cdk/overlay";
import {
  ProductSpecificationUpsertComponent
} from "../productspecification-upsert/productspecification-upsert.component";
import {IProductSpecification} from "../../models/productspecification.model";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";

@Component({
  selector: 'app-productspecification-list',
  templateUrl: './productspecification-list.component.html',
  styleUrls: ['./productspecification-list.component.scss']
})
export class ProductSpecificationListComponent extends BaseListComponentV2<IProductSpecification> {

  config: ProductSpecificationConfig = defaultProductSpecificationConfig
  productSpecifications: IPage<IProductSpecification> = defaultPage

  protected componentName(): string {
    return "ProductSpecificationListComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              dialog: DialogService<IProductSpecification>,
              private activatedRoute: ActivatedRoute,
              private productSpecificationService: ProductSpecificationService,
              private notifier: NotifierService,
  ) {
    super(eventBus, router, subContainer, dialog)
    if (authService.isAdmin()) {
      this.config.searchEndpoint = InventoryEndpoints.PRODUCTSPECIFICATIONS.PRODUCTSPECIFICATION_ENDPOINT_ADMIN
    }
  }

  initialize(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      let filterData = parseFilterData(params)
      this.fetchProductSpecifications({
        q: filterData.query,
        username: filterData.username,
        from_date: filterData.fromDate,
        to_date: filterData.toDate,
        page: filterData.page,
        size: filterData.size
      })
    });
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "ProductSpecifications",
        link: null,
        active: true
      }
    ]
  }

  private fetchProductSpecifications(params: IParams) {
    this.loading = true
    const key = this.componentKey(EventKey.PRODUCTSPECIFICATIONS.SEARCH(params))
    this.productSpecificationService.search(key, this.config.searchEndpoint, params)
    this.subscribe(
      key,
      null,
      (value: Boolean, meta: MetaData) => {
        this.productSpecifications = meta.data
        this.loading = false
      })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      this.notifier.error('Error', meta.data.error.message)
    })
  }

  onDeleted(deleted: Boolean) {
    if (deleted) {
      this.notifier.success("Success", "ProductSpecification is deleted")
      this.ngOnInit()
    }
  }

  override getConfig(): BaseConfig {
    return this.config
  }


  override getRoutes(): { create: string, update: (id: number) => string, params: IParams } | null {
    return {
      create: selfRoutes.create,
      update: (id: number) => selfRoutes.update(id),
      params: {}
    }
  }

  getUpsertComponent(): ComponentType<BaseUpsertComponentV2<IProductSpecification>> | null {
    return ProductSpecificationUpsertComponent
  }

}
