import {Component, EventEmitter, Input, Output} from '@angular/core';
import {BaseComponent} from "../../../../../../../base/components/base.component";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {AuthService} from "../../../../../../common/modules/auth/services/auth.service";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";
import {IParams} from "../../../../../../../base/endpoints/iparams";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {IProduct, IProductBrief, toProductBrief} from "../../models/product.model";
import {InventoryEndpoints} from "../../../inventory.endpoints";
import {ProductService} from "../../services/product.service";
import {DialogService} from "../../../../../../common/modules/utility/components/popup/dialog.service";
import {ProductUpsertComponent} from "../product-upsert/product-upsert.component";


@Component({
  selector: 'app-product-select',
  templateUrl: './product-select.component.html',
  styleUrls: ['./product-select.component.scss']
})
export class ProductSelectComponent extends BaseComponent {

  @Input() searchEndpoint: string = InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_V2

  items: IProductBrief[] = []
  @Input() selectedItem: IProductBrief | null = null
  @Output() selected: EventEmitter<IProductBrief | null> = new EventEmitter<IProductBrief | null>()

  protected override componentName(): string {
    return "ProductSelectComponent";
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              authService: AuthService,
              private productService: ProductService,
              private dialogService: DialogService<IProduct>) {
    super(eventBus, router, subContainer)

    if (authService.isAdmin()) {
      this.searchEndpoint = InventoryEndpoints.PRODUCTS.PRODUCT_ENDPOINT_ADMIN_V2
    }
  }

  override ngOnInit(): void {
    this.loading = false
    this.search({
      page: 0, size: 10, q: null
    })
  }

  searchForEvent(event: any) {
    let query = TextUtility.removeSpecialCharacters(event.target?.value)
    if (query.length > 2) {
      this.search({
        page: 0, size: 10, q: query
      })
    }
  }

  onSelected(event: any) {
    // this.selected.emit(this.selectedItem)

    if (this.items.find(c => c.name == event.name)) {
      this.selected.emit(this.selectedItem)
    } else {
      let dialogRef = this.dialogService.open(
        ProductUpsertComponent, {id: null, name: event.name, dialogUpsert: true}
      )
      let component = dialogRef.componentInstance
      component.onCompleted.subscribe((product: IProduct) => {
        this.selected.emit(toProductBrief(product))
      })
      component.onCanceled.subscribe(() => {
        dialogRef.close()
      })
    }
  }

  search(params: IParams) {
    this.loading = true
    let key = this.componentKey(EventKey.PRODUCTS_V2.SEARCH(params))
    this.productService.search(key, this.searchEndpoint, params)
    this.subscribe(key, null, (value, meta) => {
      this.items = meta.data.content
      this.loading = false
    })
  }
}
