import {Component, Inject} from '@angular/core';
import {FormArray, FormControl, FormGroup, Validators} from "@angular/forms";
import {BaseUpsertComponentV2} from "../../../../../../../base/components/base-upsert-v2.component";
import {defaultFilterableAttributeConfig, FilterableAttributeConfig, selfRoutes} from "../../models/config.model";
import {EventBusService} from "../../../../../../../services/eventbus/event-bus.service";
import {ActivatedRoute, Router} from "@angular/router";
import {SubscriptionContainerService} from "../../../../../../../base/services/subscription-container.service";
import {FilterableAttributeService} from "../../services/filterableattribute.service";
import {NotifierService} from "../../../../../../../services/notifier/notifier.service";
import {MAT_DIALOG_DATA} from "@angular/material/dialog";
import {BreadcrumbItem} from "../../../../../components/breadcrumb/breadcrumb.model";
import {EventKey} from "../../../../../../../services/eventbus/event.key";
import {HttpErrorResponse} from "@angular/common/http";
import {IFilterableAttribute} from "../../models/filterableattribute.model";
import {BaseConfig} from "../../../../../../../base/models/baseConfig";
import {TextUtility} from "../../../../../../common/modules/utility/text.utility";

@Component({
  selector: 'app-filterableattribute-upsert',
  templateUrl: './filterableattribute-upsert.component.html',
  styleUrls: ['./filterableattribute-upsert.component.scss']
})
export class FilterableAttributeUpsertComponent extends BaseUpsertComponentV2<IFilterableAttribute> {

  config: FilterableAttributeConfig = defaultFilterableAttributeConfig

  filterableAttributeForm = new FormGroup({
    name: new FormControl('', [Validators.required, Validators.minLength(3)]),
    code: new FormControl('', [Validators.required, Validators.minLength(3)]),
    values: new FormArray([], Validators.required)
  })

  get valuesArrayControls(): FormControl[] {
    return (this.filterableAttributeForm.get('values') as FormArray).controls
      .filter(control => control instanceof FormControl) as FormControl[];
  }

  addValueControl(value: string) {
    const control = new FormControl(value, Validators.required);
    (this.filterableAttributeForm.get('values') as FormArray).push(control);
  }

  removeValue(index: number) {
    const valuesArray = this.filterableAttributeForm.get('values') as FormArray;
    valuesArray.removeAt(index); // Remove control at the specified index
  }

  constructor(eventBus: EventBusService,
              router: Router,
              subContainer: SubscriptionContainerService,
              route: ActivatedRoute,
              private filterableAttributeService: FilterableAttributeService,
              private notifier: NotifierService,
              @Inject(MAT_DIALOG_DATA) dialogData: any) {
    super(eventBus, router, subContainer, route, dialogData)

    if (dialogData.name != null) {
      this.filterableAttributeForm.controls.name.setValue(dialogData.name)
      this.filterableAttributeForm.controls.code.setValue(TextUtility.codify(dialogData.name))
    }
  }

  protected override componentName(): string {
    return "FilterableAttributeUpsertComponent"
  }

  protected override getBreadcrumbItems(): BreadcrumbItem[] {
    return [
      {
         text: this.translations["Dashboard"],
        link: null,
        active: false
      },
      {
        text: "FilterableAttributes",
        link: "/admin/filterableattributes",
        active: false
      },
      {
        text: "Upsert",
        link: null,
        active: true
      }
    ]
  }

  override ngOnInit() {
    super.ngOnInit();
    /*
   update form `code` when `name` changes
    */
    this.filterableAttributeForm.controls.name.valueChanges.subscribe(value => {
      if (value == null) return
      this.filterableAttributeForm.controls.code.setValue(TextUtility.codify(value))
    })

    /*
    Add a default value control
     */
    if (this.exId == null) {
      this.addValueControl('')
      this.addValueControl('')
    }
  }

  override initialize(exId: number): void {
    this.loading = true
    let key = this.componentKey(EventKey.FILTERABLEATTRIBUTES.GET(exId))
    this.filterableAttributeService.fetchSingle(key, exId)
    this.subscribe(key, null, (value, meta) => {
      this.loading = false;
      this.populateForm(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      console.log(error)
      this.loading = false;
    })
  }

  onSubmit() {
    if (!this.filterableAttributeForm.valid) {
      return
    }
    this.upsert(this.exId)
  }

  getConfig(): BaseConfig {
    return this.config;
  }

  getListPageRoute(): string {
    return selfRoutes.list;
  }

  upsert(exId: number | null) {
    this.loading = true
    let key = this.componentKey(EventKey.FILTERABLEATTRIBUTES.UPSERT(exId))
    // @ts-ignore
    this.filterableAttributeService.upsert(key, this.filterableAttributeForm.value, exId)
    this.subscribe(key, null, (value, meta) => {
      this.filterableAttributeForm.reset();
      this.onSuccess(meta.data)
    })
    this.subscribe(this.getErrorKey(key), null, (value, meta) => {
      let error: HttpErrorResponse = meta.data
      this.loading = false;
      this.notifier.warning('Failed!', error.error.message)
    })
  }

  private populateForm(n: IFilterableAttribute) {
    this.filterableAttributeForm.controls.name.setValue(n.name)
    this.filterableAttributeForm.controls.code.setValue(n.code)
    n.values.forEach(value => {
      this.addValueControl(value)
    })
  }

}
